import React from 'react';
import { createStyles, Theme, withStyles, WithStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';
import Typography from '@material-ui/core/Typography';
import TweetCard from '../tweetCard/tweetCard.component';
import TweetInput from '../tweetInput/tweetInput.component';
const styles = (theme: Theme) =>
  createStyles({
    root: {
      margin: 0,
      padding: theme.spacing(3),
    },
    closeButton: {
      position: 'absolute',
      left: theme.spacing(1),
      top: theme.spacing(0),
      color: `#1DA1F2`,
      
    },
  });

export interface DialogTitleProps extends WithStyles<typeof styles> {
  id: string;
  children: React.ReactNode;
  onClose: () => void;
}

const DialogTitle = withStyles(styles)((props: DialogTitleProps) => {
  const { children, classes, onClose, ...other } = props;
  return (
    <MuiDialogTitle disableTypography className={classes.root} {...other}>
      <Typography variant="h6">{children}</Typography>
      {onClose ? (
        <IconButton aria-label="close" className={classes.closeButton} onClick={onClose}>
          <CloseIcon />
        </IconButton>
      ) : null}
    </MuiDialogTitle>
  );
});

const DialogContent = withStyles((theme: Theme) => ({
  root: {
    // padding: theme.spacing(4),
  },
}))(MuiDialogContent);

const DialogActions = withStyles((theme: Theme) => ({
  root: {
    // margin: 0,
    // padding: theme.spacing(1),
  },
}))(MuiDialogActions);

export default function CustomizedDialogs({tweet, open, setOpen, setChange, index}:{tweet:any, open:boolean, setOpen:Function, index:number, setChange:Function}) {


  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>

      <Dialog onClose={handleClose} aria-labelledby="customized-dialog-title" open={open}>
        <DialogTitle id="customized-dialog-title" onClose={handleClose}>
          
        </DialogTitle>
        <DialogContent dividers>
        <TweetCard tweet={{ ...tweet, index: index, setChange }} noBorder={false} />
            <TweetInput setChange={setChange} key={`tweetReplayIdkey`} type={"replay"} index={index} setOpen={setOpen} />
        </DialogContent>

      </Dialog>
    </div>
  );
}